<template>
	<div class="notes" v-if="$store.state.userIsAuthenticated && this.$store.state.userEmail">
        <div class="user">
            <div class="user-data" v-if="$store.state.userData">
                <h2>Hello {{$store.state.userData.username}}</h2>
            </div>
            
            <div class="create-user" v-if="$store.state.userToBeCreated">
                <CreateUser></CreateUser>
            </div>
        </div>
        <NotesList></NotesList>
        <NewNote v-if="$store.state.all.length > 0"></NewNote>
	</div>
</template>

<script>
import CreateUser from "./CreateUser.vue";
import NotesList from "@/components/NotesList.vue";
import NewNote from "@/components/NewNote.vue";

export default {
    name: "Notes",
    components:{
    CreateUser,
    NotesList,
    NewNote
},
	data() {
		return {
		};
	},
	methods: {
	},
    beforeCreate(){
        if (this.$store.state.userIsAuthenticated) {
            if(!this.$store.state.userEmail){
                this.$store.dispatch("auth0getUserEmail");
            }
		}
    }
};
</script>

