<template>
	<div class="notes-list-container">
        <!-- <span v-if="$store.state.all.length > 0">
            <span v-if="getLinkedNotes()"></span>
        </span> -->
		<div
			class="notes-list"
			v-if="$store.state.all.length > 0"
			v-for="(note, i) in $store.state.all"
		>
			<h4 class="date"
				v-if="
					i == 0 ||
					($store.state.all[i - 1] &&
						note.createdAt.substring(0, 10) !=
							$store.state.all[i - 1].createdAt.substring(0, 10))
				"
			>
				{{ note.createdAt.substring(0, 10) }}
			</h4>

			<div
				class="card"
                v-if="!linked_notes.includes(note._id)"
				:class="{ active: note == selected_note }"
				@click="select(note)"
			>
				<div class="active-note-line">
					<span class="note-text">
						<input
							class="edit-note-input"
							v-if="note == selected_note"
							v-model="note.I"
							v-on:keypress.enter="editNote(note)"
						/>
						<input
							v-else
							class="edit-note-input"
							v-model="note.I"
							readonly
						/>
					</span>
					<span class="note-buttons" v-if="note == selected_note">
						<button
							class="edit-enter-button"
							@click="editNote(note)"
						>
							
						</button>
						<button class="add-button" @click="toggleAddingLink()">
							+
						</button>
					</span>
				</div>
				<LinkList
					:toAdd="note == selected_note && add_link_to_selected_note"
					:note="note"
				></LinkList>
			</div>
		</div>
		<div v-else>
			<h3>Loading user data...</h3>
		</div>
	</div>
</template>

<script>
import LinkList from "@/components/LinkList.vue";
export default {
	components: {
		LinkList,
	},
	data() {
		return {
			selected_note: null,
			add_link_to_selected_note: false,
			new_link: "",
            linked_notes: []
		};
	},
	methods: {
		getDingByLink(link) {
			let id = Object.values(link)[0];
			let ding = this.$store.state.all.filter((x) => x._id == id)[0] ?? {
				I: "! " + id,
			};
			if (ding.I[0] == "!") {
				console.log("ding not found");
				return false;
			}
			return ding;
		},
		select(note) {
			if (this.selected_note != note) {
				this.selected_note = note;
				this.add_link_to_selected_note = false;
			}
		},
		deselect() {
			this.selected_note = null;
		},
		editNote(note) {
			this.$store.dispatch("updateNote", {
				note: note,
				callback: (note) => {},
			});
			this.deselect();
		},
		toggleAddingLink() {
			this.add_link_to_selected_note = !this.add_link_to_selected_note;
		},
        getLinkedNotes(){
            console.log(this.$store.state.all.filter(y => y.O.length>0).map(x=>x.O.map(z=>Object.values(z)[0])).flat());

            this.linked_notes =this.$store.state.all.filter(y => y.O.length>0).map(x=>x.O.map(z=>Object.values(z)[0])).flat();

            // only remove those who are already displayed
        }
	},
	mounted() {
		this.$store.dispatch("init");
	},
};
</script>

