<template>
	<div class="new-note">
		<InputBar @commit="createNote"></InputBar>
	</div>
</template>

<script>
import InputBar from "./InputBar.vue";
export default {
	components: {
		InputBar,
	},
	data() {
		return {};
	},
	methods: {
		createNote(value) {
			if (value.length > 0) {
				this.$store.commit("createNote", {
					note: value,
					callback: (note) => {},
				});
				this.new_note = "";
			}
		},
	},
};
</script>

<style>
.new-note {
	margin-bottom: 0.5em;
}
</style>