<template>
  	<div class="input-bar">
		<input
			v-model="value"
			v-on:keypress.enter="commit()"
		/>
        <button class="enter-button" @click="commit()"></button>
	</div>
</template>

<script>
export default {
    name:"InputBar",
    props:[],
    data(){
        return{
            value: ""
        }
    },
    methods:{
        commit(){
            this.$emit('commit',this.value);
            this.value = "";
        }
    }

}
</script>

<style scoped>

.input-bar{
    display:flex;
}

input {
	float: left;

	font: inherit;
	font-size: inherit;
	color: inherit;

	margin-left: 0.5em;
	padding: 0.3em;
	padding-left: 0.5em;

	width: calc(100%);
	box-sizing: border-box;

	border: solid;
	border-width: 1px;
	border-radius: 4px;
	outline: none;
}

.enter-button{
    border: solid;
    border-width: 1px;
    border-radius: 5px;
	font-size: 1.4em;
