<template>
	<div
		class="active-note-links">
		<div class="links-list" v-for="link in note.O">
			<li>{{ getDingByLink(link).I }}</li>
		</div>

        <li class="links-list" v-if="toAdd">
            <InputBar class="link-list-input-bar" @commit="AddLinkToSelectedNote"></InputBar>
        </li>
	</div>
</template>

<script>
import InputBar from "./InputBar.vue";
export default {
    components:{
        InputBar
    },
    props:['note','toAdd'],
    data() {
		return {
		};
	},
    methods:{
        getDingByLink(link) {
            let id = Object.values(link)[0];
			let ding = this.$store.state.all.filter((x) => x._id == id)[0] ?? {
				I: "! " + id,
			};
			if (ding.I[0] == "!") {
				console.log("ding not found");
				return false;
			}
			return ding;
		},
        AddLinkToSelectedNote(value) {
			if (this.note && value.length > 0) {
				this.$store.dispatch("addLinkToNote", {
					note: this.note,
					link: value,
					callback: (note) => {
					},
				});
			}
		},
    }
};
</script>

